body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgba(33,54,65,1) 0%, rgba(1,8,9,0) 100%);
  background: linear-gradient(to left, rgba(33,54,65,1) 0%, rgba(1,8,9,0) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
font-family: 'SEGA LOGO FONT';
font-style: normal;
font-weight: normal;
font-size: xx-large;
src: local('SEGA LOGO FONT'), url(/RoboFriends/static/media/SEGA.12a3db8b.woff) format('woff');
}
h1{
	font-weight: 10;
	font-family: 'SEGA LOGO FONT';

}
